import { createContext, ReactNode, useContext, useMemo, useState } from "react";

import { BusinessTimeZoneInterface } from "Shared/Interfaces/Business/BusinessProfile";
import countriesData from "Constants/Data/countries.json";
import timeZonesData from "Constants/Data/time-zones.json";
import industriesData from "Constants/Data/industries.json";

import { BizSettingsApiContextState, BizSettingsDataContextState } from "./types";

const BizSettingsData = createContext<BizSettingsDataContextState | undefined>(
  undefined
);
const BizSettingsApi = createContext<BizSettingsApiContextState | undefined>(
  undefined
);

const BizSettingsProvider = ({ children }: { children: ReactNode }) => {
  const countriesList: any[] = countriesData;
  const [provinces, setProvinces] = useState<any[]>([]);
  const industryList: string[] = industriesData;
  const timeZoneList: BusinessTimeZoneInterface[] = timeZonesData;
  const data: BizSettingsDataContextState = {
    timeZoneList,
    industryList,
    countriesList,
    provinces,
  };
  const api = useMemo<BizSettingsApiContextState>(
    () => ({
      setProvinces,
    }),
    []
  );

  return (
    <BizSettingsApi.Provider value={api}>
      <BizSettingsData.Provider value={data}>{children}</BizSettingsData.Provider>
    </BizSettingsApi.Provider>
  );
};

export const useBizSettingsData = () => {
  const context = useContext(BizSettingsData);
  if (context === undefined) {
    throw Error(
      "Item must be used inside of SettingsProvider, otherwise it will not function correctly."
    );
  }
  return context;
};

export const useBizSettingsApi = () => {
  const context = useContext(BizSettingsApi);
  if (context === undefined) {
    throw Error(
      "Item must be used inside of SettingsProvider, otherwise it will not function correctly."
    );
  }
  return context;
};

export default BizSettingsProvider;
